body
{
  display: flex;
  justify-content: center;
  height: 100%;
  background-color: #c4c6c7d4 !important;
}

.outerContainer
{
  margin-top: 30px;
  width: 800px;
  height: auto;
  box-sizing: border-box;
  padding: 20px;
  background-color: #898c8c66;
}

.innerContainer
{
  display: flex;
  width: 100%;
  height: 150px;
  justify-content: space-between;
  margin-bottom: 50px;
}
  
.outerRowButton
{
  width: 220px;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.outerButtonFetch
{
  width: 220px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.buttonFetch
{
  box-sizing: border-box;
  width: 100%;
  height: 60px;
  margin-bottom: 10px;
}

.buttonRow
{
  width: 100px;
  height: 60px;
}

.formInput
{
  margin-bottom: 100px;
  height: 500px;
}

/* Make clicks pass-through */
#nprogress 
{
  pointer-events: none;
}
  
#nprogress .bar 
{
  background: #17a2b8;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 20px;
}
  
/* Fancy blur effect */
#nprogress .peg 
{
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #17a2b8;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
      -ms-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner 
{
  display: block;
  position: fixed;
  z-index: 1031;
  top: 25px;
  right: 10px;
}

#nprogress .spinner-icon 
{
  width: 35px;
  height: 35px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent 
{
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar 
{
  position: absolute;
}

@-webkit-keyframes nprogress-spinner 
{
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner 
{
  0%   { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
